import { NgModule } from '@angular/core';
import { UIRouterModule, RootModule, Ng2StateDeclaration } from 'ui-router-ng2';

import { HomeComponent, DemoComponent } from './containers';

import { AppContainersModule } from './containers/app-containers.module';

// States

const homeState = {
  name: 'home',
  url: '/',
  component: HomeComponent
};

const demoState = {
  name: 'demo',
  url: '/demo',
  component: DemoComponent
};

// END - States

const routes = {
  states: [
    homeState,
    demoState
  ],
  useHash: false,
  otherwise: {
    state: homeState.name
  }
};

@NgModule({
  imports: [
    AppContainersModule,
    UIRouterModule.forRoot(routes)
  ],
  exports: [UIRouterModule]
})
export class AppRoutingModule { }



