import { Component, OnInit, SimpleChanges, ViewChild } from '@angular/core';
import { FileUploader, FileItem } from 'ng2-file-upload';
import { Log } from 'ng2-logger/ng2-logger';
import { Helpers } from '../../helpers/helpers';
import { MicroBlinkService } from '../../services/microblink/microblink.service';

@Component({
  selector: 'app-demo',
  templateUrl: './demo.component.html',
  styleUrls: ['./demo.component.scss']
})
export class DemoComponent implements OnInit {

  @ViewChild('selectedFiles')
  public selectedFiles: any;
  public uploader: FileUploader = new FileUploader({});
  public hasBaseDropZoneOver = false;
  public microblinkResult: any;
  public isExecutionInProgress: boolean;
  public isInputFileValid: boolean;

  private logger: any;

  constructor(private microblinkService: MicroBlinkService) {
    this.logger = Log.create('DemoComponent');
    this.uploader.onAfterAddingFile = this.onAfterAddingFile;
  }

  ngOnInit() {
  }

  /**
   * File over drop base detector
   * @param e is true when file is over base box
   */
  public fileOverDropBase(e: any): void {
    this.hasBaseDropZoneOver = e;
  }

  /**
   * When file is added to uploader.queue
   */
  private onAfterAddingFile = (fileItem: FileItem) => {

    // 1. extract File object
    const inputFile: File = fileItem._file;

    // 2. before execution inspect is file valid
    this.isInputFileValid = this.microblinkService.isInputFileValid(inputFile);

    // 3. abort execution if file is not valid
    if (!this.isInputFileValid) {
      this.logger.er('invalidInputFile', inputFile);
      this.logger.i('abort microblink.execute');
      return;
    }

    // 4. reset input field
    this.selectedFiles.nativeElement.value = '';

    // 5. start execution
    this.isExecutionInProgress = true;

    // 6. convert file (image) to base64
    Helpers.fileToBase64(inputFile).subscribe((imageBase64: string) => {
      // 7. remove file from queue
      this.uploader.clearQueue();

      // 8. set recognizer type
      const recognizerType = 'MRTD';

      // 9. execute MicroBlink API
      this.microblinkService.execute(recognizerType, imageBase64).subscribe((microblinkResult: any) => {
        // 10. extract results
        this.logger.i('microblinkResult', microblinkResult);
        this.microblinkResult = microblinkResult;
        this.isExecutionInProgress = false;
      });

    });
  }

}



